/**
 * 
 * @param  {String} text    
 * @param  {String} keyword 
 * @param  {Object} config  { tag:  html , className:  html  class  }
 * @return {String}         
 */
export default function markMatch (
  text: string,
  keyword: string,
  config: { tag?: string, className?: string } = {}
): string {
  const { tag = 'span', className = 'match' } = config
  // tslint:disable-next-line
  if (!keyword || !(keyword = keyword.trim())) {
    return text
  } else {
    const escapedKeyword = keyword.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&')
    const reg = new RegExp(escapedKeyword, 'gi')
    return text.replace(reg, (match) => `<${tag} class="${className}">${match}</${tag}>`)
  }
}



